














































@import "@/assets/scss/variables.scss";
article.document {
  left: $sidebar-size;
  @include breakpoint(mobile) {
    left: 0px;
    height: calc(100% - #{$sidebar-size});
  }
  z-index: 4;
  background-color: white;
  max-width: $max-document-width;
  overflow-y: scroll;
  overscroll-behavior-y: contain;
  -webkit-overflow-scrolling: touch;
  padding: 0 30px;

  h3 {
    font-size: 33px;
    line-height: 35px;
    @include breakpoint(mobile) {
      font-size: 26px;
      line-height: 28px;
    }
  }

  .media-gallery {
    margin-left: -30px;
    margin-right: -30px;
  }

  p {
    font-size: 18px;
    line-height: 28.44px;
  }

  @include breakpoint(mobile) {
    .actions {
      position: fixed;
    }
  }
}
