
























































































































.media-gallery {
  figure {
    position: relative;
    & > *:not(.placeholder) {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0px;
      width: 100%;
    }
    margin: 0;
  }
  .controls {
    display: flex;
    justify-content: center;
    button {
      background: transparent;
      color: black;
      &.previous {
        margin-right: auto;
      }
      &.next {
        margin-left: auto;
      }
    }
    ul {
      display: flex;
      justify-content: center;
      list-style-type: none;
      li {
        width: 10px;
        height: 10px;
        margin: 0 3px;
        border-radius: 50%;
        background-color: grey;
        cursor: pointer;
        &.active {
          transform: scale(1.2);
          cursor: default;
        }
      }
    }
  }
  p {
    margin-top: 10px;
    padding: 0 30px;
  }
}
